.btn-outline-primary {
  color: var(--primary);
  border-color: var(--primary);

  &:hover,
  &:focus,
  &:active {
    background-color: var(--primary) !important;
    border-color: var(--primary) !important;
    color: var(--buttonTextHover) !important;
  }
}

.btn-primary {
  background-color: var(--primary);
  border-color: var(--primary);

  &:hover,
  &:focus,
  &:active {
    background-color: var(--primary);
    border-color: var(--primary);
    color: var(--buttonTextHover) !important;
  }
}

.btn {
  font-family: inherit;
  border-width: 2px;

  font-weight: 400;
  font-size: 15px;
  line-height: 18px;
  padding: 14px 16px;

  &:disabled {
    border-width: 2px !important;
    border-style: solid !important;
    border-color: #cccccc !important;
  }
}

.btn-sm {
  font-weight: 500;
  font-size: 13px;
  line-height: 16px;
  padding: 8px 10px;
}
