.alert {
  border-radius: 0px 2px 2px 0px;
  line-height: 18px;
  height: fit-content;
  flex-grow: 1;
}
.alert-container {
  display: flex;

  .alert-congratulatory {
    border: 2px solid #02a56c !important;
  }
  .alert-cautionary {
    border: 2px solid #fe8d2c !important;
  }
  .alert-prohibitive {
    border: 2px solid #ff4c3d !important;
  }
  .alert-informational {
    border: 2px solid #3b99fe !important;
  }
}
.alert-box {
  border-radius: 2px 0px 0px 2px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
  position: relative;
  min-width: 40px;
}
.alert-box-congratulatory {
  background: #02a56c;
  border: 2px solid #02a56c;
}
.alert-box-cautionary {
  background: #fe8d2c;
  border: 2px solid #fe8d2c;
}
.alert-box-prohibitive {
  background: #ff4c3d;
  border: 2px solid #ff4c3d;
}
.alert-message-icon {
  margin: 0 !important;
}
.alert-box-informational {
  background: #3b99fe;
  border: 2px solid #3b99fe;
  .alert-message-icon {
    top: 40%;
    left: 20%;
  }
}

.alert-warning {
  --bs-alert-color: #664d03;
  --bs-alert-bg: #fff3cd !important;
  --bs-alert-border-color: #ffecb5;
}
