.accordion,
.accordion-header,
.accordion-item,
.accordion-button {
  background-color: #edf2f4 !important;
}

.accordion-header .accordion-button {
  color: black !important;
  font-weight: bold;
}

.accordion-header .accordion-button:not(.collapsed) {
  border-bottom: none;
  box-shadow: none;
}

.accordion-button::after {
  display: none;
}

.nid-accordion-button {
  width: 100%;
  background-color: inherit;
  margin: 0px;
  text-align: left;
  border: none;
  display: flex;
  align-items: center;
}
