.modal-dialog {
  min-width: 400px;
  // max-width: 1200px;
}

.modal-content {
  border: 3px solid black;
  border-radius: 5px;
  padding: 25px;
}

.modal-header {
  border-bottom: none;
  padding: 0;
  margin-bottom: 30px;
}

.modal-body {
  padding: 0;
  margin-bottom: 50px;

  & > :last-child {
    margin-bottom: 0;
  }
}

.modal-title {
  font-family: inherit;
  font-weight: 700 !important;
  font-size: 21px;
  line-height: 24px;

  &.h4 {
    text-transform: none !important;
    color: black !important;
  }
}

.modal-footer {
  padding: 25px 0 0 0;
  border-top-color: #c8ccce;
  justify-content: flex-start;

  & > * {
    margin: 0;
  }

  & > :not(:last-child) {
    margin-right: 8px;
  }
}

.modal-sm {
  width: 25%;
  min-width: 400px;
  max-width: 1200px;
  margin: 0 auto;
}

.modal-md {
  width: 50%;
  min-width: 400px;
  max-width: 1200px;
  margin: 0 auto;
}

.modal-lg {
  width: 75%;
  min-width: 400px;
  max-width: 1200px;
  margin: 0 auto;
}
