:root {
  --primary-font: "Lexend", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif !important;
}

body {
  margin: 0;
  font-family: var(--primary-font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h3,
h5,
h6 {
  font-family: var(--primary-font);
  font-weight: 300;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 32px;
}

.logo-img {
  max-width: 195px;
}

.sdk-device-info-box p {
  margin: 0px;
}

.ag-grid-page-search-row {
  display: flex;
  align-items: center;
  justify-content: space-between;

  font-size: 16px;
  font-family: var(--primary-font);

  margin-bottom: 16px;
}

.ag-grid-page-search-row .pagination,
.ag-grid-page-search-row .search-box {
  display: flex;
  align-items: center;
}

.ag-grid-page-search-row .pagination select {
  font-size: 16px;
  padding: 4px;
  font-family: var(--primary-font);
}

.documentation-button {
  margin-bottom: 2px !important;
  font-size: 1em;
  border-width: 0px;
}
