.form-select,
.form-control {
  font-family: inherit;
  font-size: 14px;
  height: 56px !important;
  border-radius: 3px !important;

  &:not(.is-invalid) {
    border-color: #c8ccce;
  }

  &.is-invalid {
    & ~ .alert-icon {
      display: block;
    }
  }
}

.form-control.is-invalid {
  padding-right: 0 !important;
  background-image: none !important;
}

.form-select.is-invalid {
  padding-right: 2.25em !important;
  background-size: 16px 12px, 0 !important;
}

.form-select[data-value=''] {
  color: #7e8381;
}

.form-control::placeholder {
  color: #7e8381;
}

.form-group {
  max-width: 400px;
  position: relative;
  margin-bottom: 0;
  margin-right: 32px;
}

.divider {
  width: 100%;
  border: 1px solid #3e4440;
  opacity: 1;
  margin-top: 22px;
  margin-bottom: 22px;
}

.alert-icon {
  display: none;
  position: absolute;
  right: -20px;
  top: 28px;
  margin-top: -12px;
}

.form-label {
  font-weight: 500;
}
