.nav-tabs {
  .nav-link {
    background: #edf2f4;
    color: #7e8381;
    margin: 0px 0px;
    border: 2px solid #c8ccce;
    border-bottom: none;

    border-radius: 5px 5px 0px 0px;

    font-family: 'Lexend';
    font-weight: 400;
    font-size: 15px;
  }

  .nav-link:first-child {
    margin-left: 6px;
  }

  .nav-link.active {
    color: black;
    position: relative;
    border: 2px solid #c8ccce;
    border-bottom: none;

    &::after {
      position: absolute;
      bottom: -2px;
      left: 0px;
      content: '';
      width: 100%;
      height: 4px;
      background-color: white;
    }
  }

  ul.nav-tabs {
    border-bottom: 2px solid #c8ccce;
  }
}
