/* 
* Bootstrap currently support 4 width classNames
*  1. 25% width - 'w-25'
*  2. 50% width - 'w-50'
*  3. 75% width - 'w-75'
*  4. 100% width - 'w-100'
* Adding support for additional width percentages so that they can be used across the application
* example: 'w-32' will add a style of 'width: 32%'
*/
@for $i from 1 through 100 {
  .w-#{$i} {
    width: 1% * $i;
  }
}

/**
* similar implementation as above but in px
* example: w-px-200 will add a style of 'width: 200px`
*/
@for $i from 1 through 1000 {
  .w-px-#{$i} {
    width: 0px + $i;
  }
}

/* 
* Adding support for max width percentages so that they can be used across the application
* example: 'max-w-32' will add a style of 'max-width: 32%'
*/
@for $i from 1 through 100 {
  .max-w-#{$i} {
    max-width: 1% * $i;
  }
}

/* 
* Similar to above but with pixels
* Adding support for max width pixels so that they can be used across the application
* example: 'max-w-px-32' will add a style of 'max-width: 32px'
*/
@for $i from 1 through 1000 {
  .max-w-px-#{$i} {
    max-width: 0px + $i;
  }
}

/* 
* Adding support for min width percentages so that they can be used across the application
* example: 'min-w-32' will add a style of 'min-width: 32%'
*/
@for $i from 1 through 100 {
  .min-w-#{$i} {
    min-width: 1% * $i;
  }
}

/* 
* Similar to above but with pixels
* Adding support for min width pixels so that they can be used across the application
* example: 'min-w-px-32' will add a style of 'min-width: 32px'
*/
@for $i from 1 through 1000 {
  .min-w-px-#{$i} {
    min-width: 0px + $i;
  }
}

/* 
* Height supports in px and %
*/
@for $i from 1 through 1000 {
  .h-px-#{$i} {
    height: 0px + $i;
  }
}

@for $i from 1 through 100 {
  .h-#{$i} {
    height: 1% * $i;
  }
}

@for $i from 1 through 100 {
  .min-h-#{$i} {
    min-height: 1% * $i;
  }
}

@for $i from 1 through 100 {
  .max-h-#{$i} {
    max-height: 1% * $i;
  }
}

@for $i from 1 through 1000 {
  .min-h-px-#{$i} {
    min-height: 0px + $i;
  }
}

@for $i from 1 through 1000 {
  .max-h-px-#{$i} {
    max-height: 0px + $i;
  }
}
